  width: 150px;\n  height: 150px;\n  border-radius: 50%;\n  background: linear-gradient(45deg, transparent 40%, skyblue);\n  animation: move 0.8s linear infinite;\n  top: 40%;\n  left: 43%;\n}\n\n.loader::before {\n  position: absolute;\n  content: '';\n  top: 6px;\n  left: 6px;\n  right: 6px;\n  bottom: 6px;\n  background: rgb(241, 239, 239);\n  border-radius: 50%;\n  z-index: 2;\n}\n\n.loader::after {\n  position: absolute;\n  content: '';\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  border-radius: 50%;\n  background: linear-gradient(45deg, transparent 40%, skyblue);\n  filter: blur(20px);\n}\n@keyframes move {\n  to {\n    transform: rotate(360deg);\n    filter: hue-rotate(360deg);\n  }\n}\n\n.main {\n  width: 100%;\n  background-color: #191a1b;\n  color: white;\n  min-height: 100vh;\n  &__inner {\n    h1 {\n      text-align: center;\n      font-weight: 600;\n      font-size: 48px;\n      line-height: 20px;\n      color: #01a7e1;\n      padding: 80px 0;\n    }\n    .form {\n      padding: 20px;\n      margin: 0 auto;\n\n      &__control {\n        padding: 40px 56px;\n        max-width: 643px;\n        display: flex;\n        flex-direction: column;\n        align-items: center;\n        gap: 50px;\n        margin: 0 auto;\n\n        & input {\n          width: 100%;\n          background: #fff;\n          border-radius: 8px;\n          max-width: 530px;\n          height: 50px;\n          font-weight: 400;\n          font-size: 18px;\n          line-height: 22px;\n          color: #000000;\n          padding: 0 21px;\n        }\n\n        &-register {\n          width: 100%;\n          display: flex;\n          flex-direction: column;\n          gap: 1px;\n        }\n      }\n      &__mode {\n        width: 100%;\n        text-align: center;\n        &-items {\n          display: flex;\n          justify-content: space-between;\n          position: relative;\n          padding: 25px 0 0 0;\n\n          .form__mode-radio {\n            & label {\n  