n    padding: 10px 35px;\n  }\n\n  &__inner {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    position: relative;\n    overflow: hidden;\n    padding: 30px;\n    gap: 50px;\n    font-size: 70px;\n    font-weight: 500;\n    line-height: 90px;\n    animation: new-score 0.5s;\n\n    input {\n      text-align: center;\n      border: 2px solid #000;\n      border-radius: 8px;\n      padding: 15px;\n      font-size: 70px;\n      font-weight: 600;\n      line-height: 90px;\n      max-width: 170px;\n\n      &::-webkit-outer-spin-button,\n      &::-webkit-inner-spin-button {\n        -webkit-appearance: none;\n        margin: 0;\n      }\n    }\n  }\n  &__result {\n    display: flex;\n    flex-direction: column;\n    gap: 15px;\n    align-items: center;\n  }\n}\n\n.win {\n  font-size: 70px;\n  font-weight: 700;\n  text-align: center;\n  position: relative;\n  display: inline-block;\n}\n\nh3 {\n  margin-top: 30px;\n  font-size: 26px;\n}\n.quiz__center {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  padding: 50px 30px;\n}\n\n.timer,\n.user {\n  color: #01a7e1;\n  font-size: 20px;\n}\n\n.progress {\n  width: 100%;\n  margin: 20px auto;\n  border-radius: 10px;\n  background-color: #fff;\n  border: 1px solid #01a7e1;\n  transition-duration: 0.2s;\n\n  &__bar {\n    height: 20px;\n    background-color: #01a7e1;\n    border-radius: 10px;\n    transition: all 2s;\n  }\n}\n\n.plus-one {\n  color: #118c11;\n  position: absolute;\n  left: 48%;\n  animation-name: fade-in-up;\n  animation-duration: 3s;\n  animation-iteration-count: 1;\n  animation-timing-function: ease;\n  display: none;\n  font-size: 50px;\n}\n\n.minus-one {\n  color: #d13e15;\n  position: absolute;\n  left: 48%;\n  animation-name: fade-in-up;\n  animation-duration: 3s;\n  animation-iteration-count: 1;\n  animation-timing-function: ease;\n  display: none;\n  font-size: 50px;\n}\n\n@keyframes fade-in-up {\n  from {\n    top: 70%;\n    opacity: 1;\n  }\n\n  to {\n    top: 20%;\n    opacity: 0.5;\n  }\n}\n\n.score-leave {\n  animation: leave-score 0.5s;\n}\n\n@keyframes new-score {\n  from {\n    transform: translateX(100%);\n  }\n  to {\n    transform: translateX(0%);\n  }\n}\n@keyframes leave-score {\n  from {\n    transform: translateX(0%);\n  }\n  to {\n    transform: translateX(-100%);\n  }\n}\n\n.win-error {\n  animation: shake 0.5s;\n  animation-iteration-count: infinite;\n}\n\n@keyframes shake {\n  0% {\n    transform: translate(1px, 1px) rotate(0deg);\n  }\n  10% {\n    transform: translate(-1px, -2px) rotate(-1deg);\n  }\n  20% {\n    transform: translate(-3px, 0px) rotate(1deg);\n  }\n  30% {\n    transform: translate(3px, 2px) rotate(0deg);\n  }\n  40% {\n    transform: translate(1px, -1px) rotate(1deg);\n  }\n  50% {\n    transform: translate(-1px, 2px) rotate(-1deg);\n  }\n  60% {\n    transform: translate(-3px, 1px) rotate(0deg);\n  }\n  70% {\n    transform: translate(3px, 1px) rotate(-1deg);\n  }\n  80% {\n    transform: translate(-1px, -1px) rotate(1deg);\n  }\n  90% {\n    transform: translate(1px, 2px) rotate(0deg);\n  }\n  100% {\n    transform: translate(1px, -2px) rotate(-1deg);\n  }\n}\n\n@media (max-width: 992px) {\n  .quiz {\n    padding: 0;\n\n    &__center {\n      padding: 0 30px;\n    }\n    &__inner {\n      gap: 14px;\n      font-size: 50px;\n\n      input {\n        max-width: 90px;\n        padding: 6px;\n        font-size: 50px;\n      }\n    }\n\n    &__btn {\n      padding: 10px 25px;\n    }\n\n    &__main {\n      h1 {\n        font-size: 38px;\n      }\n    }\n  }\n  .win {\n    font-size: 50px;\n  }\n}\n\n@media (max-width: 556px) {\n  .timer,\n  .user {\n    color: #01a7e1;\n    font-size: 16px;\n  }\n\n  .quiz__center-answer {\n    font-size: 16px;\n  }\n\n  .quiz__result {\n    gap: 10px;\n  }\n\n  .quiz__info {\n    padding: 30px 0 60px;\n  }\n}\n",".bg-modal {\n  background-color: rgba(0, 0, 0, 0.35);\n  position: fixed;\n  top: 0;\n  bottom: 0;\n  right: 0;\n  left: 0;\n  display: flex;\n  justify-content: center;\n  align-items: start;\n  opacity: 0;\n  visibility: hidden;\n  z-index: 20;\n  overflow-y: auto;\n}\n\n.score__form {\n  margin: 20px;\n  position: relative;\n  max-width: 466px + 30px;\n  height: 700px;\n  background-color: #ffffff;\n  opacity: 1;\n  padding: 30px 60px;\n  border-radius: 4px;\n\n  &-score {\n    display: flex;\n    align-items: center;\n    flex-direction: column;\n    p {\n      font-weight: 400;\n      font-size: 30px;\n      line-height: 61px;\n    }\n    .score__current {\n      font-weight: 400;\n      font-size: 50px;\n      line-height: 134px;\n    }\n  }\n\n  &-info {\n    h1 {\n      font-weight: 400;\n      font-size: 46px;\n      line-height: 85px;\n    }\n  }\n\n  &-rating {\n    p {\n      font-weight: 400;\n      font-size: 24px;\n      line-height: 37px;\n    }\n  }\n  &-btns {\n    padding: 50px 0;\n    display: flex;\n    flex-direction: column;\n    gap: 15px;\n    align-items: center;\n    a {\n      padding: 10px 25px;\n    }\n    .score-btn {\n      background-color: #01a7e1;\n      padding: 10px 25px;\n    }\n  }\n}\n\n@media (max-width: 556px) {\n  .score__form-info {\n    h1 {\n      font-size: 36px;\n      line-height: 45px;\n    }\n  }\n}\n",".leaders {\n  background-color: #191a1b;\n  height: 100vh;\n  padding-top: 40px;\n\n  &__empty {\n    margin: 0 auto;\n    text-align: center;\n    padding: 50px 0;\n    font-weight: 400;\n    font-size: 30px;\n    line-height: 37px;\n  }\n\n  &__inner {\n    padding: 40px;\n    max-width: 50%;\n    margin: 0 auto;\n    background: #ffffff;\n    box-shadow: 0px 4px 40px 1px rgba(0, 0, 0, 0.1);\n    border-radius: 20px;\n\n    span {\n      padding: 50px 0;\n      font-weight: 400;\n      font-size: 30px;\n      line-height: 37px;\n    }\n\n    h1 {\n      font-weight: 400;\n      font-size: 46px;\n      line-height: 85px;\n    }\n\n    select {\n      max-width: 293px;\n      border: 3px solid #000000;\n    }\n  }\n\n  &__mode {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    gap: 13px;\n  }\n\n  &__back {\n    display: flex;\n    align-items: center;\n    gap: 10px;\n    font-weight: 400;\n    font-size: 30px;\n    line-height: 37px;\n    padding-bottom: 20px;\n\n    img {\n      width: 40px;\n    }\n  }\n\n  &__item {\n    display: flex;\n    gap: 20px;\n    font-weight: 400;\n    font-size: 30px;\n    line-height: 49px;\n\n    &-score {\n      font-weight: 600;\n    }\n  }\n}\n\n@media (max-width: 992px) {\n  .leaders {\n    padding: 20px;\n    &__inner {\n      max-width: 70%;\n      h1 {\n        font-size: 36px;\n        line-height: 35px;\n      }\n    }\n  }\n}\n\n@media (max-width: 556px) {\n  .leaders {\n    &__inner {\n      max-width: 95%;\n      h1,\n      span {\n        font-size: 26px;\n        line-height: 35px;\n      }\n    }\n    &__back {\n      font-size: 16px;\n      line-height: 45px;\n    }\n  }\n}\n"],"sourceRoot":""}]);
